.blog-repay {
    font-size: 13px;
    line-height: 1.75em;
    color: #000 !important;
    margin: 0;
    font-weight: 400;
}
/* pagination */
.blog-pagination-wrap {
    padding: 0;
    margin: 30px 0 0 0;
    text-align: center;
}
.blog-pagination-wrap li {
    display: inline-block;
    margin: 0 5px;
    border-radius: 100%;
    border: 1px solid #f5eeeb;
}
.blog-pagination-wrap li a {
    background: #fff;
    display: inline-block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #888;
    font-weight: 300;
    border-radius: 100%;
}
.blog-pagination-wrap li a:hover {
    opacity: 1;
    text-decoration: none;
    background-color: #BD945A;
    border: 1px solid #BD945A;
    color: #fff;
}
.blog-pagination-wrap li a.active {
    background-color: #BD945A;
    border: 1px solid #BD945A;
    color: #fff;
}
.blog-pagination-wrap li a i { 
    font-size: 11px;
}
@media screen and (max-width: 768px) {
    .blog-pagination-wrap {
        padding: 0 0 60px 0;
        margin: 0;
        text-align: center;
    }
}



/* ==== Media Query ==== */
@media screen and (max-width: 991px) {
    
    .container {
        padding-right: 30px;
        padding-left: 30px;
    }
    .oliven-couple-title {
        font-size: 32px;
        line-height: 1.2em;
        margin-bottom: 15px;
    }
    .oliven-couple-subtitle {
        font-size: 18px;
    }
    #countdown .section-head h4 {
        font-size: 32px;
        line-height: 1em;
    }
    .seeyou .section-head h4 {
        font-size: 32px;
        margin-bottom: 5px;
        line-height: 1em;
    }
    .seeyou .section-head h3 {
        font-size: 32px;
    }  
      
    .p-40 {
        padding: 20px;
        margin: 0 15px;
    }
}

